@media screen and (min-width: 701px) {
  #container {
    display: grid;
    grid-template-columns: 15% 15% auto 15% 15%;
    grid-template-rows: 10% 5% auto 5%;
    padding: 10px; }
  #image-header-desktop {
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 2;
    grid-row-end: 4; }
  #nav-container {
    align-items: center;
    display: flex;
    flex-direction: "row";
    justify-content: center; }
  .nav {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    background-color: #2665C4;
    border-radius: 5px;
    padding: 55px;
    margin-bottom: 10px;
    cursor: pointer;
    border-right: 0px;
    border-left: 0px;
    padding: 10px;
    color: white; }
  #content {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 3;
    background-color: white;
    border-radius: 5px;
    padding: 25px 55px 55px 55px; }
  #request-box {
    grid-column-start: 5;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 4; }
  .headline {
    font-family: sans-serif;
    color: #2665C4;
    font-size: 22px;
    margin-bottom: 50px; } }
