.subheadline {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 15px;
  margin: 25px 0px 5px 0px; }

.text {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 15px; }

.list {
  font-family: sans-serif;
  color: #2665C4;
  font-size: 15px; }

.nav a,
.nav a:visited {
  font-family: sans-serif;
  text-decoration: none;
  color: white; }

.nav a:active,
.nav a:hover {
  font-family: sans-serif;
  text-decoration: none;
  color: #03c203; }

.list a,
.list a:visited,
.list a:active {
  font-family: sans-serif;
  color: #2665C4;
  text-decoration: none; }

.list a:hover {
  font-family: sans-serif;
  color: #03c203;
  text-decoration: none; }
